import { Box, Button, ButtonGroup, makeStyles, Typography, Link, Modal } from "@material-ui/core";
import React, { useState, useEffect, useRef } from "react";
import { useWeb3React } from "@web3-react/core";
import { getContract } from "src/utils";
import abi from "src/constants/ABI/ABI.json";

export const Game = () => {
  const useStyles = makeStyles((theme) => ({
    frame: {
      marginTop: "120px",
      paddingTop: "5px",
      height: "650px",
      width: "1000px",
    },
    text: {
      marginTop: "140px"
    }
  }));
  const classes = useStyles();

  const [balance, setBalance] = useState(0);

  const { account, library, chainId } = useWeb3React();
  const contractJob = getContract("0xe707a98108C31C77dDee267fDe07EFC8A8Bf86cc", abi, library, account);

  useEffect(() => {
    account &&
      contractJob["balanceOf(address)"](account)
        .then((e) => {
          balance ? e != balance && setBalance(e) : setBalance(e);
        })
        .catch((e) => console.log(e));
  }, [account, balance]);
  return account ? balance > 0 ? (
    <>
      <iframe src="https://peculiarpugs.io/barkade.test/index.html" className={classes.frame} />
    </>
  ) : (
    <Link target="_blank" href="https://mint.peculiarpugs.io">
      <Typography className={classes.text}>Sorry you don't have a pug, click here to adopt yours today!</Typography>
    </Link>
  ) : (
      <Typography className={classes.text}>Connect your wallet to play in the Barkade</Typography>
  );
};
